import React from "react";
import {motion} from "framer-motion";

function ProjectCard(props){

    const rotateFront=()=>{
        const pro=document.getElementById("pro"+props.id);
        const bpro=document.getElementById("bpro"+props.id);
        pro.animate([{transform:'rotateY(0)'},{transform:'rotateY(180deg)'}],{duration:500,iterations:1,fill:"forwards"});
        bpro.animate([{transform:'rotateY(180deg)'},{transform:'rotateY(0deg)'}],{duration:500,iterations:1,fill:"forwards"});
    }
    const rotateBack=()=>{
        const pro=document.getElementById("pro"+props.id);
        const bpro=document.getElementById("bpro"+props.id);
        bpro.animate([{transform:'rotateY(0)'},{transform:'rotateY(180deg)'}],{duration:500,iterations:1,fill:"forwards"});
        pro.animate([{transform:'rotateY(180deg)'},{transform:'rotateY(0deg)'}],{duration:500,iterations:1,fill:"forwards"});
    }

    
    const Varients={offScreen:{y:200,
        opacity:0
        },
        onScreen:{y:0,
            opacity:1
        },
        transition:{
            type:"spring",
            stiffness:100
        },
        exit:{
            y:-100
        }
};

    return <motion.div
            drag
            dragConstraints={{
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
            }}
            dragElastic={1}
            initial="offScreen" whileInView={"onScreen"} transition={"transition"} variants={Varients}
            className="cardDiv">
            

            <div id={"bpro"+props.id} className="bpro" onClick={(e)=>{
                console.log("clicked bpro");
                if(e.target.id!=="vbtn")
                    rotateBack();
            }}>
            <h1 className="appName">{props.ele.name}</h1>
            <p className="aboutApp">{props.ele.about}</p>
            <a className="visitbtn" rel="noreferrer" id="vbtn" href={props.ele.app} target="_blank">visit</a>
            </div>
            <div id={"pro"+props.id} className="pro" style={{backgroundImage: `url(${props.ele.img})`}} onClick={(e)=>{
                console.log("clicked pro");
                rotateFront();
            }}>
            <h1 className="appName">{props.ele.name}</h1>
            </div>
        </motion.div>
}

export default ProjectCard;