import React,{useState,useEffect} from "react";
import "./A&C.css"
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';

function Achiev(){
  
  const [curidx,setidx]=useState(0);
  const [l,setL]=useState(0);
  const interval=React.useRef();

  const moveRight=()=>{
    clearTimeout(interval.current);
    setidx(curidx+1);
    console.log(curidx+1);
    if(curidx+1>2){
      setidx(0);
      setL(0);
      return;
    }
    setL(-(curidx+1)*100);
  }
  
  useEffect(()=>{
    interval.current=setTimeout(moveRight,4000);
  });
  
  const change=(val)=>{
    clearTimeout(interval.current);
    setidx(Number(val)-1);
    setL(-(val-1)*100);
    console.log(val);
  }

  const moveLeft=()=>{
    clearTimeout(interval.current);
    setidx(curidx-1);
    console.log(curidx-1);
    if(curidx-1<0){
      setidx(2);
      setL(-2*100);
      return;
    }
      setL(-(curidx-1)*100);
    }

  return (
    <div className="Achiev">
      <div className="slide" style={{transform:`translateX(${l}%)`}}>
          <div className="cert">
              <h1>Certifications</h1>
              <div className="innerAc">
              <h3>Full Stack WebDevelopment from Udemy &ensp;&emsp;&ensp;&ensp;&emsp;&emsp;&emsp;<i>03-2022 - 08-2022</i></h3>
              <h3>Java Full Stack from Cipher Schools &emsp;&emsp; &emsp;&emsp;&emsp;&emsp;&emsp;<i>03-2022 - 04-2022</i></h3>
              <h3>JavaScript for WebDevelopment from Board Infinity &emsp;<i>04-2020 - 06-2020</i></h3>
              </div>
          </div>
      </div>
      <div className="slide" style={{transform:`translateX(${l}%)`}}>
          <div className="achiev">
              <h1>Achievements</h1>
              <div className="innerAc">
              <h3>#157 global Rank in CodeChef feb long challenge 2022</h3>
              <h3>participated in google developer I/O 2022 &emsp;&emsp;&emsp;&emsp;&ensp;</h3>
              <h3>Solved more than 300 problems in geeksforgeeks&emsp;&emsp;&ensp;</h3>
              </div>
          </div>
      </div>
      <div className="slide" style={{transform:`translateX(${l}%)`}}>
          <div className="believ">
          <h1>{"Actions >>> Talks"}</h1>
          </div>
      </div>
      <button onClick={moveRight} className="btnright"><KeyboardArrowRightIcon sx={{fontSize:50}} /></button>
      <button onClick={moveLeft} className="btnleft"><KeyboardArrowLeftIcon sx={{fontSize:50}}/></button>
      <div className='cbtnDiv'>
        <button className='cbtn' style={{opacity: curidx===0? 1:0.2}} value={1} onClick={(e)=>{
          change(e.target.value);
        }}>&ensp;</button>
        <button className='cbtn' style={{opacity: curidx===1? 1:0.2}} value={2} onClick={(e)=>{
          change(e.target.value);
        }}>&ensp;</button>
        <button className='cbtn' style={{opacity: curidx===2? 1:0.2}} value={3} onClick={(e)=>{
          change(e.target.value);
        }}>&ensp;</button>
      </div>
    </div>
  );
}

export default Achiev;