import React from "react";
import "./Projects.css"
import ProjectCard from "./components/cards"
import keep from "./images/keep.png";
import blog from "./images/blog.png";
import todo from "./images/todo.png";
import simon from "./images/simon.png";
import flap from "./images/flap.png";
import {motion} from "framer-motion";
// import OpenInFullIcon from '@mui/icons-material/OpenInFull';

function Projects(){

    const procards=[{
        id:1,
        name:"Keep-notes",
        about:"This is a full-stack project which uses mongodb, react js, node js, HTML and css. It also has open authetication and is able to store sessions in it for a period of time.",
        img:keep,
        app:"https://make-note-app.herokuapp.com/"
    },
    {
        id:2,
        name:"Blog-Project",
        about:"This stimulates a blog website in which we can create edit and delete the blogs in it. This is developed using mongodb, node js, embedded js, HTML and css.",
        img:blog,
        app:"https://blog-app-stimulate.herokuapp.com/"
    },
    {
        id:3,
        name:"Custom To-Do-list",
        about:"This app can have multiple todo lists with different names click on addLists it will show all the lists in the app the click on the desired one to make changes This is developed using node js, ejs, mongodb, HTML and css.",
        img:todo,
        app:"https://young-hamlet-17849.herokuapp.com/"
    },
    {
        id:4,
        name:"Simon Game",
        about:"This is a game where the game stats by pressing any key then you should follow the pattern and click accordingly. This is developed using HTML, css, js.",
        img:simon,
        app:"https://saitejaarava.github.io/Simon-Game/"
    },
    {
        id:5,
        name:"flappy bird",
        about:"This stimulates the game of flappy bird where you need to cross the obstacles to score points. This is made using HTML, css, js.",
        img:flap,
        app:"https://saitejaarava.github.io/flappy-bird/"
    }];


    return <div className="Projects">
        <motion.h1 initial={{x:-300,opacity:0}} whileInView={{x:0,opacity:1}} viewport={{amount:0.8}} className="AboutMe">Projects</motion.h1>
        <hr className="topLine pl" />
        <div className="prodiv">
            {procards.map((x,index)=><ProjectCard  key={index} id={index} ele={x}/>)}
        </div>
        {/* <OpenInFullIcon className="arrow" /> */}
    </div>
}

export default Projects;