import React from "react"
import "./Skills.css"
import SettingsIcon from '@mui/icons-material/Settings';
import {motion,AnimatePresence} from "framer-motion";

function Skills(){
    const SkillVarients={offScreen:{y:200,
                        opacity:0
                        },
                        onScreen:{y:0,
                            opacity:1
                        },
                        transition:{
                            type:"spring",
                            stiffness:100
                        },
                        exit:{
                            y:-100
                        }
};

    return <div className="MainSkills">
    <AnimatePresence>
        <motion.h1  initial={{x:-300,opacity:0}} whileInView={{x:0,opacity:1}} viewport={{amount:0.8}}  exit="exit" className="AboutMe">About Me</motion.h1>
        <hr className="topLine" />
        <SettingsIcon className="bullseye" sx={{fontSize:350}}/>
        <motion.div initial="offScreen" whileInView={"onScreen"} transition={"transition"} variants={SkillVarients} exit="exit" className="Skills">
            <h1 className="SkillTitle" >Skills</h1>
            <div className="leftSkills">
                <h4 className="mySkill">java</h4>
                <h4 className="mySkill">c/c++</h4>
                <h4 className="mySkill">SQL</h4>
                <h4 className="mySkill">HTML</h4>
                <h4 className="mySkill">CSS</h4>
            </div>
            <div className="rightSkills">
                <h4 className="mySkill">JavaScript</h4>
                <h4 className="mySkill">React js</h4>
                <h4 className="mySkill">Node js</h4>
                <h4 className="mySkill">EJS</h4>
                <h4 className="mySkill">Mongodb</h4>
            </div>
        </motion.div>
        <motion.div initial="offScreen" whileInView={"onScreen"} transition={"transition"} variants={SkillVarients} exit="exit" className="Skills inteDiv" id="ruler">
            <div className="intrests">
                <h1 className="SkillTitle inteTitle">Intrests</h1>
                <h4 className="inte">Problem Solving</h4>
                <h4 className="inte">Web Development</h4>
                <h4 className="inte">Editing</h4>
                <h4 className="inte">Sports</h4>
                <h4 className="inte">Music</h4>
            </div>
        </motion.div>
        <motion.div initial="offScreen" whileInView={"onScreen"} transition={"transition"} variants={SkillVarients} exit="exit" className="Skills LangDiv">
            <h1 className="SkillTitle inteTitle">Languages</h1>
            <h4 className="inte">English</h4>
            <h4 className="inte">Hindi</h4>
            <h4 className="inte">Telugu</h4>
        </motion.div>
    </AnimatePresence>
    </div>
}
export default Skills;